import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Col, Row } from "reactstrap";
import {
  createUserSkillsData,
  deleteUserSkillData,
  getUserSkillsData,
} from "../reducers/skillReducer";

import ProfileSkillsSection from "./ProfileSkillsSection";
import AnimatedElement from "./AnimatedElement";
import { useNavigate } from "react-router-dom";

const Skills = (props) => {
  const { userdetails } = useSelector((state) => state.user);
  const displayType = props.displayType;
  const [skillData, setSkillData] = useState(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const getUserSkills = async () => {
    const data = await dispatch(
      getUserSkillsData({ token: userdetails.token })
    );
    if (!data.payload.error) {
      console.log(data.payload);
      await setSkillData(data.payload);
    } else {
      await setSkillData(null);
    }
  };

  useEffect(() => {
    if (props.updateSkill) {
      getUserSkills();
    }
  }, [props.updateSkill]);

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      createUserSkillsData({
        id: id,
        active: active,
        token: userdetails.token,
      })
    );
    if (data) {
      let tempSkill = [...skillData];
      await skillData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempSkill[i] = data.payload;
        }
      });
      setSkillData(tempSkill);
    }
  };

  const editSkills = (sName, sHeading, sImage, sDesc, id) => {
    props.editSkills(sName, sHeading, sImage, sDesc, id);
  };

  const deleteSkills = async (id) => {
    const data = await dispatch(
      deleteUserSkillData({ id: id, token: userdetails.token })
    );
    getUserSkills();
  };
  return (
    <Row>
      {skillData &&
        skillData.map((item, i) =>
          displayType && displayType === "block" ? (
            item.active && (
              <Col className="col-6 col-md-3 my-2" key={item._id}>
                <AnimatedElement>
                  <div
                    className="s-back text-center p-2"
                    onClick={(e) => navigate(`/skills/${item._id}`)}
                  >
                    <p className="f-size-15 m-0 p-0">{item.skillName}</p>
                  </div>
                </AnimatedElement>
              </Col>
            )
          ) : displayType && displayType === "column" ? (
            item.active && (
              <Col className="col-6 col-md-4" key={item._id}>
                <AnimatedElement>
                  <div className="text-start p-2">
                    <p
                      className="f-size-20 m-0 p-0 t-color cursor-pointer"
                      onClick={(e) => navigate(`/skills/${item._id}`)}
                    >
                      {item.skillName}
                    </p>
                  </div>
                </AnimatedElement>
              </Col>
            )
          ) : (
            <Col
              className="col-12 skill-item position-relative px-0 px-md-4"
              style={{ overflowX: "hidden" }}
              key={item._id}
            >
              <ProfileSkillsSection
                item={item}
                updateStatus={updateStatus}
                editSkills={editSkills}
                deleteSkills={deleteSkills}
              />
            </Col>
          )
        )}
    </Row>
  );
};

export default Skills;
